import React, { useEffect, useState } from "react";
import "blip-toolkit/dist/blip-toolkit.css";
import { getApplication } from "./api/applicationService";
import { showToast, withLoading } from "./api/commomService";
import { PageHeader } from "components/PageHeader";
import { CommonProvider } from "contexts/CommonContext";
import { PageTemplate } from "components/PageTemplate";
import { BlipTabs } from 'blip-toolkit'

import Movidesk from 'components/Movidesk';
import LogoCloneBots from './static/images/parse_cloneBots_v2.png';
import Doc from 'components/Doc/index';
import { set_builder } from './constants/application-actions'
import { BdsIcon } from 'blip-ds/dist/blip-ds-react';

function App() {

        const [application, setApplication] = useState({ shortName: '', accessKey: '', key: '' });

        useEffect(async () => {
            withLoading(async () => {

                const { accessKey, shortName } = await getApplication();
                const KEY = 'Key ' + btoa(`${shortName}:` + atob(accessKey));
                setApplication({ shortName, accessKey, key: KEY });

                showToast({
                    type: "success",
                    message: "Extensão carregada com sucesso!"
                });

                new BlipTabs('tab-nav');
            });

        }, []);

        const title = `Extensão Movidesk`;
        const logo = LogoCloneBots;
        const keyTarget = application.key;
        const builder = set_builder;

        return (

            <>
                <CommonProvider>
                    <div id="main" className="App">
                        <PageHeader title={title} logo={logo} />
                        <PageTemplate title={title}>
                            <div id="tab-nav" className="bp-tabs-container">
                                <ul className="bp-tab-nav">
                                    <li>
                                        <a href="#" data-ref="configuracao">Configuração</a><BdsIcon theme="outline" type="icon" name="settings-general" />
                                    </li>
                                    <li>
                                        <a href="#" data-ref="doc">Documentação</a><BdsIcon theme="outline" type="icon" name="file-txt" />
                                    </li>
                                </ul>
                                <div className="bp-tab-content fl w-100" data-ref="configuracao">
                                    <Movidesk info={keyTarget} builder={builder} />
                                </div>
                                <div className="bp-tab-content fl w-1000" data-ref="doc">
                                    <Doc />
                                </div>
                            </div>
                        </PageTemplate>
                    </div>
                </CommonProvider>
    
            </>
        )
        
   

   

}

export default App